<script>
	import Layout from "../../layouts/main";
	import PageHeader from "@/components/page-header";
	import axios from 'axios';
	/**
	 * written by 张文彧
	 */
	export default {
		components: {
			Layout,
			PageHeader
		},
		data() {
			return {
				title: "主机管理",
				userId:'',
				auth:'',
				form: {
					id: '',
					userName: '',
					userPasswrod: '',
					userStatus: '',
					userAuth: '',
					userPhone: '',
					userEmail: '',
					lastLoginTime: '',
					departmentId: '',
				},
				formLabelWidth: '120px',
				dialogFormVisible: false,
				items: [{
						text: "主机管理"
					},
					{
						text: "主机列表",
						active: true
					}
				],
				userList: [{
						orderid: "#NZ1563",
						date: "28 Mar, 2020",
						billingname: "Frank Dean",
						total: "$164",
						paymentstatus: "Unpaid"
					},

				],
				totalRows: 1,
				currentPage: 1,
				perPage: 10,
				pageOptions: [10, 25, 50, 100],
				filter: null,
				filterOn: [],
				sortBy: "id",
				sortDesc: false,
				fields: [{
						key: "id",
						sortable: true,
						label: "主机ID"
					},
					{
						key: "userId",
						sortable: false,
						label: "用户ID"
					},
					{
						key: "hostName",
						sortable: false,
						label: "主机名"
					},
					{
						key: "hostIp",
						sortable: false,
						label: "主机IP"
					},
					{
						key: "hostMac",
						sortable: false,
						label: "MAC地址"
					},
					{
						key: "hostUserName",
						sortable: false,
						label: "主机用户名"
					},
					{
						key: "hostUserPassword",
						sortable: false,
						label: "主机密码"
					},
					{
						key: "hostUerPort",
						sortable: false,
						label: "主机端口"
					},
					{
						key: "hostUserProtocol",
						sortable: false,
						label: "主机协议"
					},
					{
						key: "createTime",
						sortable: true,
						label: "创建时间"
					},
					// { key: "invoice" },
					{
						key: "action"
					}
				]
			};
		},
		computed: {

			/**
			 * Total no. of records
			 */
			rows() {
				return this.userList.length;
			}
		},
		mounted() {
			
		},
		
		created() {
			this.userId=sessionStorage.getItem('id'),
			this.auth=sessionStorage.getItem('auth'),
			console.log(this.userId)
			this.getAll()
			
		},
		methods: {
			/**
			 * @param {Object} str
			 * 省略密码的长度
			 */

			ommit(str) {
				if (str.length > 8) {
					return str.substring(0, 8) + '...'
				} else {
					return str
				}
			},
			/**
			 * Search the table data with search input
			 */
			/**
			 * @param {Object} scope
			 * 用于提示删除操作
			 */

			editUser() {
				this.dialogFormVisible = false
				console.log(this.form)
				axios
					.post('http://localhost:8848/users/updateById', this.form)
					.then(response => (
						// this.userList = response.data.list
						console.log(response),
						this.$message({
							type: 'success',
							message: '修改成功!'
						}),
						this.getAll()
					))
					.catch(function(error) { // 请求失败处理
						console.log(error);
					});
			},
			/**
			 * 获取所有数据
			 */
			getAll() {
				if(this.auth==1){
					axios
						.get('http://localhost:8848/hosts/selectByUserId',{params:{userId:this.userId}})
						.then(response => (
						
							this.userList = response.data.obj,
							console.log(this.userList),
							// Set the initial number of items
							this.totalRows = this.userList.length
						))
						.catch(function(error) { // 请求失败处理
							console.log(error);
						});
				} else{
					axios
						.get('http://localhost:8848/hosts/selectAll')
						.then(response => (
							this.userList = response.data.list,
							console.log(this.userList),
							// Set the initial number of items
							this.totalRows = this.userList.length
						))
						.catch(function(error) { // 请求失败处理
							console.log(error);
						});
				}
				
			},
			open(scope) {
				this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.delUser(scope)
					
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消删除'
					});
				});
			},
			/**
			 * @param {Object} scope
			 * 弹出修改窗
			 */
			openEdit(scope) {
				console.log(scope)
				this.dialogFormVisible = true,
					this.form.id = scope.item.id
				this.form.userName = scope.item.userName
				this.form.userPasswrod = scope.item.userPasswrod
				this.form.userPhone = scope.item.userPhone
				this.form.userEmail = scope.item.userEmail
				this.form.userStatus = scope.item.userStatus
				this.form.userAuth = scope.item.userAuth
				this.form.departmentId = scope.item.departmentId
			},
			/**
			 * @param {Object} scope
			 * 请求删除接口
			 */
			

			onFiltered(filteredItems) {
				// Trigger pagination to update the number of buttons/pages due to filtering
				this.totalRows = filteredItems.length;
				this.currentPage = 1;
			},
			delUser(scope){
				console.log(scope.item.id),
				axios
					.post('http://localhost:8848/hosts/deleteById',null,{params:{id:scope.item.id}})
					.then(response => (
						// this.userList = response.data.list
						console.log(response),
						this.$message({
							type: 'success',
							message: '删除成功!'
						}),
						this.getAll()
					))
					.catch(function(error) { // 请求失败处理
						console.log(error);
				});
			}

		},

	};
</script>
<template>
	<Layout>
		<PageHeader :title="title" :items="items" />
		<el-dialog title="收货地址" :visible.sync="dialogFormVisible">
			<el-form :model="form">
				<el-form-item label="用户名" :label-width="formLabelWidth">
					<el-input v-model="form.userName" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="密码" :label-width="formLabelWidth">
					<el-input v-model="form.userPasswrod" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="用户状态" :label-width="formLabelWidth">
					<el-input v-model="form.userStatus" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="用户权限" :label-width="formLabelWidth">
					<el-input v-model="form.userAuth" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="手机号" :label-width="formLabelWidth">
					<el-input v-model="form.userPhone" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="邮箱" :label-width="formLabelWidth">
					<el-input v-model="form.userEmail" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="部门ID" :label-width="formLabelWidth">
					<el-input v-model="form.departmentId" autocomplete="off"></el-input>
				</el-form-item>
				<!-- <el-form-item label="活动区域" :label-width="formLabelWidth">
					<el-select v-model="form.region" placeholder="请选择活动区域">
						<el-option label="区域一" value="shanghai"></el-option>
						<el-option label="区域二" value="beijing"></el-option>
					</el-select>
				</el-form-item> -->
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisible = false">取 消</el-button>
				<el-button type="primary" @click="editUser()">确 定</el-button>
			</div>
		</el-dialog>
		<div class="row">
			<div class="col-lg-12">
				<div class="card">
					<div class="card-body pt-0">
						<b-tabs nav-class="nav-tabs-custom">
							<b-tab title-link-class="p-3">
								<template v-slot:title>
									<a class="font-weight-bold active">主机列表</a>
								</template>
								<div class="row mt-4">
									<div class="col-sm-12 col-md-6">
										<div id="tickets-table_length" class="dataTables_length">
											                     <label class="d-inline-flex align-items-center">
                        &nbsp;
                        <b-form-select v-model="perPage" size="sm" :options="pageOptions"></b-form-select>&nbsp;条
                      </label>
										</div>
									</div>

									<!-- Search -->
									<div class="col-sm-12 col-md-5">
										
										<div id="tickets-table_filter" class="dataTables_filter text-md-right">
											<label class="d-inline-flex align-items-center">
												Search:
												<b-form-input v-model="filter" type="search"
													class="form-control form-control-sm ml-2"></b-form-input>
											</label>
										</div>
									</div>
									<!-- End search -->
								</div>
								<div class="table-responsive">
									<b-table class="table-centered table-hover" :items="userList" :fields="fields" responsive="sm"
										:per-page="perPage" :current-page="currentPage" :sort-by.sync="sortBy"
										:sort-desc.sync="sortDesc" :filter="filter" :filter-included-fields="filterOn"
										@filtered="onFiltered">
										<template v-slot:cell(userPasswrod)="row">
											<div class="badge font-size-12">{{ommit(row.value)}}</div>
										</template>

										<template v-slot:cell(paymentstatus)="row">
											<div class="badge font-size-12" :class="{'badge-soft-danger': `${row.value}` === 'Chargeback',
                            'badge-soft-success': `${row.value}` === 'Paid',
                            'badge-soft-warning': `${row.value}` === 'Unpaid'}">{{ row.value}}</div>
										</template>
										<template v-slot:cell(action)="scope">

											<!-- <a href="javascript:void(0);" class="mr-3 text-primary" v-b-tooltip.hover
												title="Edit" @click="openEdit(scope)">
												<i class="mdi mdi-pencil font-size-18"></i>
											</a> -->

											<a href="javascript:void(0);" class="text-danger" v-b-tooltip.hover
												title="Delete" @click="open(scope)">
												<i class="mdi mdi-trash-can font-size-18"></i>
											</a>

										</template>
									</b-table>
								</div>
								<div class="row">
									<div class="col">
										<div class="dataTables_paginate paging_simple_numbers float-right">
											<ul class="pagination pagination-rounded mb-0">
												<!-- pagination -->
												<b-pagination v-model="currentPage" :total-rows="rows"
													:per-page="perPage"></b-pagination>
											</ul>
										</div>
									</div>
								</div>
							</b-tab>
						</b-tabs>
					</div>
				</div>
			</div>
		</div>
	</Layout>
</template>
